import React from "react";
import "./About.css";

function About() {
  return (
    <>
      <div style={{ paddingBottom: "5%", backgroundColor: "#004d00" }}>
        <h1
          style={{
            paddingTop: "2%",
            textAlign: "center",
            color: "white",
          }}
        >
          Sobre
        </h1>
        <div
          style={{
            marginTop: "5%",
            width: "60%",
            margin: "3% auto",
            backgroundColor: "white",
            borderRadius: "20px",
            paddingBottom: "3%",
          }}
        >
          <div className="form" id="form">
            <div className="pt-5">
              {/* <h4 style={{ color: "green" }}>Motivao</h4> */}
              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                O ingresso no ensino superior requer que estudantes procurem
                moradia, principalmente se ainda no residem na mesma cidade
                onde estudaro. Alm disso, compartilhar uma moradia ajuda a
                reduzir os custos gerais e proporciona uma maior convivncia com
                colegas do campus. Muitas vezes essa busca por um lugar para
                morar precisa ser feita em um curto perodo, entre a convocao
                para matrcula e o incio das aulas, mas tambm pode ocorrer ao
                longo de toda graduao dependendo da realidade de cada um. E
                ns sabemos como essa busca pode ser difcil.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Diante desse contexto e visando contribuir com uma soluo para
                estudantes, Palloma Vilela Tiba e Victor Greghi Hernandez,
                alunos da 1 turma de Engenharia da Computao do IFSP campus
                Piracicaba, com a orientao do Prof. Dr. Thiago Lus Lopes
                Siqueira, propuseram o desenvolvimento de um software que
                objetiva viabilizar o anncio e a procura de vagas em moradias,
                bem como a formao de grupos para dividirem uma moradia.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Cada integrante contribuiu com diferentes habilidades e
                expertise que juntas resultaram nessa plataforma. O Victor
                Greghi agregou com a modelagem de dados, com conhecimentos nas
                tecnologias front-end e back-end utilizadas no desenvolvimento e
                com operaes no banco de dados. Alm disso, sua influncia como
                presidente da Atltica do campus contribuiu para recrutar alunos
                na fase de teste e continuar contribuindo na etapa de
                divulgao da plataforma para os estudantes.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                J a integrante Palloma Tiba auxiliou no desenho do projeto, sua
                viso de design contribuiu para criao da parte visual do site,
                criao do nosso nome e logo. Alm disso, ela tambm contribuiu
                no desenvolvimento front-end, back-end e banco de dados.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Esse projeto associa-se com dois Objetos de Desenvolvimento
                Sustentvel (ODS). O objetivo 11.1, no qual refere-se a garantir
                o acesso de todos  habitao segura, adequada e a preo
                acessvel e com o objeto 4 como um todo, no que se refere ao
                acesso  educao de qualidade.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Apesar de existirem outras plataformas disponveis para busca de
                moradia, o IFHome se diferencia por ser pensado e criado
                especialmente para voc, estudante. Nossa misso  facilitar,
                cada dia mais, sua insero no ambiente acadmico. Para isso,
                buscamos evoluir nossa plataforma diariamente para atender as
                suas necessidades.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                <strong>Obrigado por utilizar o IFHome!</strong>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default About;
