import React from "react";

import "./filterStudents.css";

let FilterStudents = (props) => {
  function onFilterValueChanged(event) {
    props.filterValueSelected(event.target.value);
  }

  return (
    <div className="filter-area">
      <div>Filtrar estudantes por curso: &nbsp;</div>
      <div>
        <select onChange={onFilterValueChanged} className="selectCurso">
          <option value="Todos">Todos</option>
          <option value="Engenharia da Computao">
            Engenharia da Computao
          </option>
          <option value="Engenharia Mecanica">Engenharia Mecnica</option>
          <option value="Engenharia Eltrica">Engenharia Eltrica</option>
          <option value="Fsica">Fsica</option>
          <option value="Automao Industrial">Automao Industrial</option>
        </select>
      </div>
    </div>
  );
};

export default FilterStudents;
